:root {
  --blue: #aebfc6;
}

.avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;
}

/* Container around content */
.container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 70%;
}

.container.header {
  width: 50%;
}

.timeline .container:first-child {
  text-align: left;
  width: 50%;
  padding-top: 40px;
  /* z-index: 1; */
}

.timeline .container:first-child .content::after {
  /* background: none !important;
  color: #fcfcfc; */
  content: 'EXPERIENCE';
  color: white;
  position: absolute;
  bottom: -100px;
  font-weight: bold;
  left: 10px;
}

/* The circles on the timeline */
.container:not(.nontimeline)::after {
  content: '';
  position: absolute;
  width: 15px;
  height: 15px;
  right: -17px;
  background-color: white;
  border: 4px solid var(--blue);
  top: 20px;
  border-radius: 50%;
  z-index: 1;
}

/* Place the container to the left */
.left {
  left: 0;
}

/* Place the container to the right */
.right {
  left: 2%;
}

.center {
  margin-left: auto;
  margin-right: auto;
}

/* Add arrows to the left container (pointing right) */
.center:not(.nontimeline)::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 0;
  width: 0;
  z-index: 1;
  right: 50%;
  border: medium solid white;
  border-width: 0 10px 10px 10px;
  border-color: white transparent;
}

/* Fix the circle for containers on the right side */
.center:not(.nontimeline)::after {
  left: 50%;
  margin-left: -25px; /* width of element */
  top: -50px;
}

.nontimeline + .header {
  margin-top: 100px;
}

.timeline .container.header:first-child {
  margin-top: 50px !important; /* same as top property */
}

/* Add arrows to the left container (pointing right) */
.left:not(.nontimeline)::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid white;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent white;
}

/* Add arrows to the right container (pointing left) */
.right:not(.nontimeline)::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent white transparent transparent;
}

/* Fix the circle for containers on the right side */
.right:not(.nontimeline)::after {
  left: -11px;
}

/* The actual content */
.content {
  padding: 20px 30px;
  background-color: white;
  position: relative;
  border-radius: 6px;
}


/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
  /* Full-width containers */
  .container {
    width: 100% !important;
    padding-left: 50px;
    padding-right: 25px;
  }

  /* Make sure that all arrows are pointing leftwards */
  .container:not(.nontimeline)::before {
    left: 40px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .left:not(.nontimeline)::after, .right:not(.nontimeline)::after {
    left: 10px;
  }

  /* Make all right containers behave like the left ones */
  .right {
    left: 0%;
  }
}
